<template>
	<view class="city-search">
		<picker mode="region" @change="onCityChange">
			<view class="picker-item">
				<view v-if="currentCity">{{ currentCity.name }}</view>
				<view v-else>全部城市</view>
				<image class="select-icon" src="/static/img/icon_go@3x.png" mode="widthFix"></image>
			</view>
		</picker>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentCity: null
			}
		},
		methods: {
			onCityChange(e) {
				const code = e.detail.code.pop()
				const value = e.detail.value.pop()
				this.currentCity = {
					code,
					name: value
				}
				this.$emit('change', this.currentCity)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.picker-item {
		@include flex-c-center;
		padding: 20upx 30upx 30upx;
	}
	
	.select-icon {
		width: 28upx;
		height: 28upx;
		transform: rotate(90deg);
		margin-left: 30upx;
	}
</style>
